<template>
  <v-container>
    <v-card
      v-if="!unknown && !loading && !retake"
      class="pb-5 transparent examcontainer"
    >
      <v-card-title class="text-center">
        <h2 class="ma-auto">You Scored: {{ result[1] }}</h2>
      </v-card-title>
      <v-card-text class="text-center">
        <div class="text-center mb-n5">
          <v-btn
            tile
            class="mx-auto success my-2"
            v-if="this.ended < Date.now()"
            large
            @click="solutionv = !solutionv"
          >
            {{ !solutionv ? "View" : "Hide" }} Solution
          </v-btn>
          <v-btn
            tile
            class="mx-auto success my-2"
            v-else
            large
            @click="endalert"
          >
            View Solution
          </v-btn>
        </div>
        <iframe
          v-if="solutionv"
          :src="`https://drive.google.com/file/d/${solutionpdf.substr(
            32,
            33
          )}/preview`"
          height="700"
          width="100%"
        ></iframe>
      </v-card-text>

      <v-card-actions v-if="!retake && solution.length > 0" class="mb-n3">
        <v-btn
          :to="`/ranking/${this.$route.params.id}`"
          class="success mx-auto my-2"
          large
          tile
          >View Your Rank</v-btn
        >
      </v-card-actions>
      <v-card-actions v-if="!retake && solution.length > 0">
        <v-btn tile class="success mx-auto" @click="startRetake" large
          >Test Yourself Again</v-btn
        >
      </v-card-actions>
      <div
        class="text-center white--text"
        v-if="!retake && solution.length > 0"
      >
        Retake exam score will not be recorded
      </div>
    </v-card>
    <div v-if="retake">
      <div class="py-4 text-center">
        <h1 class="text-white">Exam - {{ this.$route.params.id }}</h1>
      </div>
      <v-card
        v-for="(q, i) in solution"
        :key="i"
        class="my-3 transparent examcontainerq"
      >
        <v-card-text class="black--text">
          <p v-html="q.question"></p>

          <v-radio-group
            v-model="solution[i].selected"
            :class="{ 'stop-event': solution[i].selected }"
          >
            <v-radio :value="q.a" v-if="q.a">
              <template v-slot:label>
                <div v-html="q.a"></div>
              </template>
            </v-radio>
            <v-radio :value="q.b" v-if="q.b">
              <template v-slot:label>
                <div v-html="q.b"></div>
              </template>
            </v-radio>
            <v-radio :value="q.c" v-if="q.c">
              <template v-slot:label>
                <div v-html="q.c"></div>
              </template>
            </v-radio>
            <v-radio :value="q.d" v-if="q.d">
              <template v-slot:label>
                <div v-html="q.d"></div>
              </template>
            </v-radio>
          </v-radio-group>
        </v-card-text>
      </v-card>

      <v-card-actions>
        <v-btn @click="retakeResult" class="success mx-auto my-2" large tile
          >Submit</v-btn
        >
      </v-card-actions>
    </div>
    <div v-if="questions.length > 0">
      <div class="py-4 text-center">
        <h1 class="white--text">Exam - {{ this.$route.params.id }}</h1>
      </div>
      <v-card v-for="(q, i) in questions" :key="i" class="my-3">
        <v-card-text class="black--text">
          <p v-html="q.question"></p>

          <v-radio-group
            v-model="questions[i].selected"
            :class="{ 'stop-event': questions[i].selected }"
          >
            <v-radio :value="q.a" v-if="q.a">
              <template v-slot:label>
                <div v-html="q.a"></div>
              </template>
            </v-radio>
            <v-radio :value="q.b" v-if="q.b">
              <template v-slot:label>
                <div v-html="q.b"></div>
              </template>
            </v-radio>
            <v-radio :value="q.c" v-if="q.c">
              <template v-slot:label>
                <div v-html="q.c"></div>
              </template>
            </v-radio>
            <v-radio :value="q.d" v-if="q.d">
              <template v-slot:label>
                <div v-html="q.d"></div>
              </template>
            </v-radio>
          </v-radio-group>
        </v-card-text>
      </v-card>
      <v-card-actions class="mb-5">
        <v-btn
          @click="submitAns"
          class="success mx-auto my-2"
          large
          tile
          :loading="eload"
          >Submit</v-btn
        >
      </v-card-actions>
    </div>
    <div class="loader" v-if="loading">
      <v-progress-circular
        :size="80"
        :width="7"
        color="error"
        indeterminate
      ></v-progress-circular>
    </div>
    <div class="white--text fixed__timer" v-if="show">
      <div class="primary">{{ minute }} : {{ second }}</div>
    </div>
  </v-container>
</template>

<script>
export default {
  data() {
    return {
      timing: null,
      solutionv: false,
      retake: false,
      questions: [],
      solution: [],
      result: [],
      unknown: false,
      loading: true,
      eload: false,
      minute: 0,
      second: 0,
      end: Date.now() + 12000000,
      show: false,
      url: "",
      solutionpdf: "",
      ended: null,
      inttime: 109090,
      startTime: Date.now(),
    };
  },
  methods: {
    endalert() {
      this.$swal({
        icon: "warning",
        title: "Will be available after the time ends",
      });
    },
    getQuestionLink() {
      fetch(
        `https://script.google.com/macros/s/AKfycbxHQvpCJacrv42u8RzaknFKc58rIQldmeY3zUB2EU4eOao0mUVk7ErwPN10r_MJREg4/exec?id=${this.$route.params.id}`
      )
        .then((res) => res.json())
        .then((data) => {
          this.url = data.exam.question;
          this.inttime = data.exam.time * 60000;
          this.end = Date.now() + data.exam.time * 60000;
          this.ended = new Date(data.exam.end).getTime();

          this.solutionpdf = data.exam.solve_sheet;
          let phone = localStorage.getItem("acuser");
          fetch(this.url + "?type=check&&phone=" + phone)
            .then((res) => res.json())
            .then((data) => {
              if (data.message == "exists" || this.ended < Date.now()) {
                this.result = data.result ? data.result : "";
                fetch(this.url + "?type=question")
                  .then((r) => r.json())
                  .then((d) => {
                    this.solution = d;
                  });

                this.loading = false;
              } else {
                this.unknown = true;
                fetch(this.url + "?type=question")
                  .then((r) => r.json())
                  .then((d) => {
                    this.questions = d.map((m) => {
                      return { ...m, selected: "" };
                    });
                    this.loading = false;
                    this.showRemaining();
                    this.autoSubmit();
                  });
              }
            });
        });
    },
    startRetake() {
      this.retake = true;
      this.showRemaining();
    },
    retakeResult() {
      let score = this.solution.reduce((a, c) => {
        if (c.correct == c.selected) {
          a += 10;
        }
        return a;
      }, 0);
      this.$swal({
        icon: "success",
        title: "Success",
        text: `You Scored ${score}`,
      }).then(() => {
        this.retake = false;
      });
    },
    submitAns() {
      this.eload = true;
      let score = this.questions.reduce((a, c) => {
        if (c.correct == c.selected) {
          a += 10;
        }
        return a;
      }, 0);
      let submitAns = localStorage.getItem(`exam${this.$route.params.id}`);
      if (!submitAns) {
        let phone = localStorage.getItem("acuser");
        fetch(this.url, {
          method: "POST",
          mode: "no-cors",
          cache: "no-cache",
          redirect: "follow",
          body: JSON.stringify({
            type: "mcq",
            mcq: [phone, score, Date.now() - this.startTime],
          }),
        }).then(() => {
          localStorage.setItem(`exam${this.$route.params.id}`, "submited");
          clearTimeout(this.timing);
          this.eload = false;
          this.$swal({
            icon: "success",
            title: "Success",
            text: `You Scored ${score}`,
          }).then(() => {
            this.$router.replace("/ranking/" + this.$route.params.id);
          });
        });
      } else {
        this.$router.replace("/ranking/" + this.$route.params.id);
      }
    },
    autoSubmit() {
      this.timing = setTimeout(() => {
        this.show = false;
        console.log("auto");
        this.submitAns();
      }, this.inttime);
    },
    showRemaining() {
      this.show = true;
      const timer = setInterval(() => {
        const distance = this.end - Date.now();
        if (distance < 0) {
          clearInterval(timer);
          return;
        }
        const days = Math.floor(distance / this._days);
        const hours = Math.floor((distance % this._days) / this._hours);
        const minutes = Math.floor((distance % this._hours) / this._minutes);
        const seconds = Math.floor((distance % this._minutes) / this._seconds);
        this.second = seconds < 10 ? "0" + seconds : seconds;
        this.minute = minutes < 10 ? "0" + minutes : minutes;
        this.hours = hours < 10 ? "0" + hours : hours;
        this.days = days < 10 ? "0" + days : days;
      }, 1000);
    },
  },

  created() {
    this.getQuestionLink();
  },
  computed: {
    _seconds: () => 1000,
    _minutes() {
      return this._seconds * 60;
    },
    _hours() {
      return this._minutes * 60;
    },
    _days() {
      return this._hours * 24;
    },
    user() {
      return this.$store.getters.getUser;
    },
  },
};
</script>

<style lang="scss" scoped>
.loader {
  display: grid;
  place-items: center;
  min-height: 30vh;
}
.fixed__timer {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  color: white;
  font-size: 22px;
  position: fixed;
  bottom: 0;
}
.fixed__timer > div {
  font-size: 20px;

  padding: 10px;

  border-radius: 5px;
}
.solution {
  border: 1px solid rgba(black, 0.2);
  border-radius: 4px !important;
  background: linear-gradient(45deg, #f7f4f4, #d8d6d6b4) !important;
  box-shadow: 2px 2px 2px #c2c0c0, -2px -2px 4px #ffffff !important;
  div {
    color: black;
  }
  p {
    color: black;
  }
}
.examcontainer {
  background: rgba(255, 255, 255, 0.09) !important;
  backdrop-filter: blur(5px);
}
.examcontainerq {
  background: rgba(255, 255, 255, 0.774) !important;
  backdrop-filter: blur(5px);
}
.stop-event {
  pointer-events: none;
  cursor: not-allowed;
}
</style>